import { gql } from '@apollo/client'

export const GET_ALL_DATA = gql`
  {
  allPortfolios{
    image{
      url
    }
    title
  }
  allSocials{
    facebook
    instagram
  }
  allPricelists{
    price
    content
    service{
      title
      fullname
    }
  }
  allServices{
    title
    fullname
  }
  description{
    message
  }
  footer{
    text
  }
  profileImg{
    mainPhoto{
      url
    }
  }
  sphere{
    words
  }
  discount{
    description
    for
    image{
      url
    }
    percent
  }
  hero{
    image{
      url
    }
  }
  contact{
    adress
    worktime
    phoneNumber
    email
  }
}
`;

