import { Icon } from '@iconify/react';
import React from 'react'
import Button from '../../utils/Button';
import { Wrapper } from './Portfolio.styles'

export default function Portfolio({data}) {
    const slicedData = data.slice(0, 3);

    return (
        <Wrapper id='portfolio'>
            <h1>Moje projekty</h1>
            <div className="projects">
                {slicedData.map((project, index) => (
                    <div className="project" key={index} data-aos='fade-up' data-aos-delay={100*index}>
                        <img src={project.image.url} alt={project.title} loading='lazy' title={project.title} />
                    </div>
                ))}
            </div>
            <Button variant='outline'>Galeria <Icon icon="akar-icons:chevron-right" /></Button>
        </Wrapper>
    )
}