import React, { useState } from 'react'
import Button from '../../utils/Button'
import Modal from './Modal'

export default function SendFormMessage({ onClose }) {
    const [isChecked, setChecked] = useState(false);
    const handleToggleCheckbox = () => {
        setChecked(prevState => !prevState)
    }

    return (
        <Modal header='Uwaga!' onClose={onClose}>
            <p>Wysłanie wiadomości nie oznacza ustalenia daty wizyty. Nastąpi to po skontaktowaniu się telefonicznie.</p>
            <p>Umawiając na wizytę akceptujesz <u>odstępy od cennika</u> oraz <u>kwoty związane z dojazdem do klienta</u>.</p>

            <p>
                <label htmlFor="checkbox"><input type="checkbox" id='checkbox' checked={isChecked} onChange={handleToggleCheckbox} /> Zgadzam się na przetwarzanie danych osobowych w celach ...</label>
            </p>
            <div className='footer'>
                <span onClick={onClose}>Anuluj</span>
                <Button disabled={!isChecked}>Wyślij wiadomość</Button>
            </div>
        </Modal>
    )
}
