import { Icon } from '@iconify/react';
import React from 'react'
import { Link } from 'react-scroll';
import { Wrapper } from './HeroScreen.styles';

export default function HeroScreen({socialmedia, hero}) {
    return (
        <Wrapper>
            <img src={hero.url} alt="Nożyczki na różowym tle." />
            <div className="text">
                <div>
                    <p className="name" data-aos='fade-up'>Ruda Elka</p>
                    <p data-aos='fade-up' data-aos-delay='100'>Mobilna Krawcowa</p>
                </div>
            </div>
            {socialmedia.map((social, index) => (
                <div className="socialmedia" key={index} data-aos='fade-left' data-aos-delay='300'>
                    <a href={social.facebook} target='_blank' rel='noreferrer'><Icon icon="akar-icons:facebook-fill" /></a>
                    <a href={social.instagram} target='_blank' rel='noreferrer'><Icon icon="akar-icons:instagram-fill" /></a>
                </div>
            ))}
            <div className="arrow">
                <Link
                    to='top'
                    spy={true}
                    smooth={true}
                    offset={-70}
                    duration={1000}>
                <Icon icon="akar-icons:chevron-down" />
                </Link>
            </div>
        </Wrapper>
    )
}