import React from 'react'
import styled from 'styled-components'

export default function Badge({children, onClick, isActive = false}) {
    return (
        <StyledBadge onClick={onClick} isActive={isActive}>
            {children}
        </StyledBadge>
    )
}
const StyledBadge = styled.button`
    cursor: pointer;
    font-size: 14px;
    padding: 0.5rem 1rem;
    margin: 0.5rem;
    border-radius: ${({theme}) => theme.borderRadius};
    background-color: ${({theme}) => theme.colors.orangeLight};
    color: ${({ theme }) => theme.colors.orange};
    transition: background-color .2s ease, color .2s ease;
    &:hover{
        background-color: ${({theme}) => theme.colors.orangeMedium};
    }
    ${({ isActive, theme }) => isActive && `
        background-color: ${theme.colors.orange};
        color: ${theme.colors.orangeLight};
        &:hover{
            background-color: ${theme.colors.orange};
        }
    `}
`;