import React from 'react'
import styled from 'styled-components'

export default function Button({children, variant, disabled=false}) {
    return (
        <StyledButton variant={variant} disabled={disabled}>
            {children}
        </StyledButton>
    )
}
const StyledButton = styled.button`
    outline: none;
    padding: 1rem 2rem;
    border-radius: ${({theme}) => theme.borderRadius};
    background-color: ${({theme}) => theme.colors.orange};
    color: ${({ theme }) => theme.colors.white};
    cursor: pointer;
    transition: background-color .2s ease;
    &:hover{
        background-color: ${({theme}) => theme.colors.orangeDark};
    }
    ${({ variant, theme }) => variant === 'outline' && `
        background-color: ${theme.colors.orangeLight};
        color: ${theme.colors.orange};
        border: 1px solid ${theme.colors.orange};
        &:hover{
            background-color: ${theme.colors.orangeMedium};
        }
        `}
    &:disabled{
        cursor: not-allowed;
        color: ${({theme}) => theme.colors.white};
        background-color: ${({theme}) => theme.colors.orangeMedium};
    }
`;