import styled from "styled-components";

export const Wrapper = styled.section`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 4rem 0;

    .projects{
        position: relative;
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin: 2rem 0;
        .project{
            margin: 1rem;
            width: 300px;
            height: 400px;
            border-radius: ${({theme}) => `calc(${theme.borderRadius}*2)`};
            overflow: hidden;
        }
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
    }
`;