import { Icon } from '@iconify/react';
import React, {useState} from 'react'
import styled from 'styled-components'
import { Link } from 'react-scroll'

export default function Nav() {
    const [isMenuActive, setMenuActive] = useState(false);

    const toggleMenu = () => {
        setMenuActive(prevState => !prevState);
    }

    return (
        <StyledNav>
            <div className='wrapper'>
                <div className="logo">
                    <Link
                        to='top'
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={1000}>
                        Ruda Elka
                    </Link>
                </div>
                <ul className={isMenuActive ? `active` : ''}>
                    <li><Link
                        onClick={toggleMenu}
                        to='top'
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={1000}>
                        Strona główna
                    </Link></li>
                    <li><Link
                        onClick={toggleMenu}
                        to='pricelist'
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={1000}>
                        Cennik
                    </Link></li>
                    <li><Link
                        onClick={toggleMenu}
                        to='portfolio'
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={1000}>
                        Portfolio
                    </Link></li>
                    <li><Link
                        onClick={toggleMenu}
                        to='contact'
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={1000}>
                        Kontakt
                    </Link></li>
                </ul>
                <div id="hamburger">
                    {isMenuActive ? <Icon icon="akar-icons:cross" onClick={toggleMenu} />
                    : <Icon icon="akar-icons:text-align-right" onClick={toggleMenu} />}
                </div>
            </div>
        </StyledNav>
    )
}
const StyledNav = styled.nav`
    position: sticky;
    top: 0;
    z-index: 11;
    width: 100%;
    height: 4rem;
    font-size: 14px;
    background-color: ${({theme}) => theme.colors.grayLight};
    text-transform: uppercase;
    .wrapper{
        max-width: 1440px;
        margin: 0 auto;
        height: 100%;
        padding: 0 2rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .logo{
        font-weight: bold;
        font-size: 1rem;
        cursor: pointer;
    }
    ul{
        list-style: none;
        display: flex;
        height: 100%;
        li{
            display: grid;
            place-items: center;
            margin-left: 2rem;
            padding: 0 .5rem;
            transition: color .2s ease;
            cursor: pointer;
            a.active{
                color: ${({theme}) => theme.colors.orange};
            }
            &:hover{
                color: ${({theme}) => theme.colors.orange};
            }
        }
    }
    #hamburger{
        position: fixed;
        top: 2rem;
        right: 2rem;
        display: none;
        font-size: 2rem;
        padding: 0.5rem;
        cursor: pointer;
        background-color: ${({theme}) => theme.colors.white};
        border-radius: ${({theme}) => theme.borderRadius};
    }
    @media screen and (max-width: 700px){
        #hamburger{
            display: flex;
        }
        ul{
            position: fixed;
            top: 0;
            right: 0;
            width: 100%;
            height: 100vh;
            margin: 0;
            flex-direction: column;
            justify-content: space-evenly;
            background-color: ${({theme}) => theme.colors.grayLight};
            transform: translateX(100%);
            transition: transform .2s ease;
            li{
                margin: unset;
            }
            &.active{
                transform: translateX(0);
            }
        }
    }
`;