import React, { useEffect, useState } from 'react'
import Badge from '../../utils/Badge';
import { Table, Wrapper } from './Pricelist.styles';

export default function Pricelist({ services, pricelist }) {
    const [activeTab, setActiveTab] = useState(services[0].title);
    const [activeArray, setActiveArray] = useState([]);

    const handleChangeTab = (tab) => {
        const tbody = document.getElementById('tbody');
        tbody.classList.add('changing');
        setTimeout(() => {
            tbody.classList.remove('changing');
            setActiveTab(tab)
        }, 400);
    }

    useEffect(() => {
        setActiveArray(pricelist.filter((item) => {
            return item.service.title === activeTab
        }));
    }, [activeTab, pricelist])

    return (
        <Wrapper id='pricelist'>
            <h1>Cennik</h1>
            <p className="small">Wybierz interesującą Cię usługę.</p>
            <p className='informations'>Sprawdź <u>odstępy od cennika</u> oraz <u>ceny dojazdu do klienta</u>.</p>
            <div className="badges">
                {services.map((service, index) => (
                    <Badge key={ index } isActive={activeTab === service.title} onClick={()=>handleChangeTab(service.title)}>{service.fullname}</Badge>
                ))}
            </div>

            <Table tabIndex='-1'>
                <thead className='header'>
                    <tr>
                        <th>Garderoba</th>
                        <th className='right'>Cena</th>
                    </tr>
                </thead>
                <tbody id='tbody'>
                    {activeArray.map((item, index) => (
                        <tr key={index}>
                            <th>{item.content}</th>
                            <th className='right'>{item.price} zł</th>
                        </tr>
                    ))}
                </tbody>
            </Table>
        </Wrapper>
    )
}