import styled, { keyframes } from "styled-components";

const flowAnimation = keyframes`
    0%{
        transform: translate(-50%, -1rem);
    }
    50%{
        transform: translate(-50%, 1rem);
    }
    100%{
        transform: translate(-50%, -1rem);
    }
`;
export const Wrapper = styled.section`
    position: relative;
    width: 100%;
    height: 100vh;
    color: white;
    user-select: none;
    z-index: 11;
    img{
        width: 100%;
        height: 100vh;
        object-fit: cover;
        object-position: center;
    }
    .text{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: rgba(0,0,0,.4);
        display: grid;
        place-items: center;
        font-size: 2rem;
        text-align: center;
        text-transform: uppercase;
        .name{
            font-size: 4rem;
            font-weight: bold;
        }
    }
    .socialmedia{
        position: absolute;
        top: 2rem;
        right: 2rem;
        font-size: 1.5rem;
        display: flex;
        a{
            display: grid;
            margin: 0 1rem;
            padding: .5rem;
            transition: transform .2s ease;
            &:hover{
                transform: scale(1.1);
            }
        }
    }
    .arrow{
        position: absolute;
        bottom: 1rem;
        left: 50%;
        transform: translateX(-50%);
        font-size: 2rem;
        cursor: pointer;
        animation: ${flowAnimation} 2s ease infinite both;
    }
    @media screen and (max-width: 800px){
        .text{
            font-size: 1rem;
            .name{
                font-size: 2rem;
            }
        }
        .socialmedia{
            right: 1rem;
            a{
                margin: 0 .5rem;
            }
        }
    }
`;