import React from 'react'
import styled from 'styled-components'

export default function About({description, image}) {
    return (
        <Wrapper>
            <div>
                <img src={image.url} alt="Elżbieta Niziołek" loading='lazy' data-aos='fade-down' />
                <p className="name" data-aos='fade-up' data-aos-delay='200'>Elżbieta Niziołek</p>
                <p className='description' data-aos='fade-up' data-aos-delay='300'>{ description.message }</p>
            </div>
        </Wrapper>
    )
}
const Wrapper = styled.section`
    background-color: ${({ theme }) => theme.colors.grayLight};
    padding: 4rem 2rem;
    text-align: center;
    div{
        max-width: 1440px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    img{
        width: 100px;
        height: 100px;
        border-radius: 100%;
    }
    .name{
        font-weight: bold;
        margin-top: 0.5rem;
    }
    .description{
        margin-top: 2rem;
        max-width: 500px;
    }
`;