import React from 'react'
import styled from 'styled-components'

export default function Footer({data}) {
    return (
        <StyledFooter>
            <div>
                <p>{ data.text }</p>
            </div>
        </StyledFooter>
    )
}
const StyledFooter = styled.footer`
    background-color: ${({ theme }) => theme.colors.black};
    color: ${({ theme }) => theme.colors.white};
    padding: 2rem;
    text-align: center;
    font-size: 14px;
    div{
        max-width: 1440px;
        margin: 0 auto;
    }
`;