import React, { useState } from 'react'
import SendFormMessage from '../modal/SendFormMessage';
import { Wrapper } from './Contact.styles';
import Form from './Form';

export default function Contact({data}) {
    const adress = data.adress.split(',');
    const worktime = data.worktime.split(',');
    const [isModalOpen, setModalOpen] = useState(false);

    const handleToggleModal = (e) => {
        e.preventDefault()
        setModalOpen(prevState => !prevState)
    }

    return (
        <Wrapper id='contact'>
            {isModalOpen && <SendFormMessage onClose={handleToggleModal} />}
            <h1>Umów wizytę</h1>
            <div className="wrapper">
                <div className="form">
                    <Form handleSubmit={handleToggleModal} />
                </div>
                <div className="contact">
                    <span>
                        <p><b>Adres</b></p>
                        <p>{adress[0]}</p>
                        <p>{adress[1]}</p>
                    </span>
                    <span>
                        <p><b>Telefon</b></p>
                        <p><a href={`tel: ${data.phoneNumber}`}>{ data.phoneNumber }</a></p>
                    </span>
                    <span>
                        <p><b>E-mail</b></p>
                        <p><a href={`mailto: ${data.email}`}>{ data.email }</a></p>
                    </span>
                    <span>
                        <p><b>Możliwość umawiania się</b></p>
                        {
                            worktime.map((time, index) => {
                                return <p key={index}>{ time }</p>
                            })
                        }
                    </span>
                </div>
            </div>
        </Wrapper>
    )
}