import React from 'react'
import { Link } from 'react-scroll';
import styled from 'styled-components'
import Button from '../../utils/Button';

export default function Discount({data}) {
    return (
        <Wrapper>
            <img src={data.image.url} alt={`Zniżka dla ${data.for}`} loading='lazy' />
            <div className="discount">
                <div>
                    <p className="percent" data-aos='fade-down' data-aos-delay='300'>Zaoszczędź {data.percent}%</p>
                    <p className="for" data-aos='fade-left'>Zniżka dla {data.for}</p>
                </div>
                <p className="description">{data.description}</p>
                <Button><Link
                        to='contact'
                        spy={true}
                        smooth={true}
                        offset={-70}
                        duration={1000}>
                        Umów wizytę już dziś!
                    </Link></Button>
            </div>
        </Wrapper>
    )
}
const Wrapper = styled.section`
    position: relative;
    height: 500px;
    display: flex;
    justify-content: right;
    overflow: hidden;
    img{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
    .discount{
        position: relative;
        width: 50%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        padding: 4rem 2rem;
        color: ${({ theme }) => theme.colors.white};
        background-color: rgba(0,0,0,.5);
        transition: width .2s ease;
        .for{
            font-size: 2.5rem;
            font-weight: bold;
        }
    }
    @media screen and (max-width: 800px){
        .discount{
            padding: 2rem;
            width: 100%;
        }
    }
`;