import { useQuery } from '@apollo/client';
import Aos from 'aos';
import React from 'react'
import { useEffect } from 'react';
import About from './components/molecules/about/About';
import Discount from './components/molecules/discount/Discount';
import Footer from './components/molecules/footer/Footer';
import HeroScreen from './components/molecules/heroScreen/HeroScreen';
import Loader from './components/molecules/loader/Loader';
import Nav from './components/molecules/nav/Nav';
import Contact from './components/organisms/contact/Contact';
import Portfolio from './components/organisms/portfolio/Portfolio';
import Pricelist from './components/organisms/pricelist/Pricelist';
import Sphere from './components/utils/Sphere';
import { GET_ALL_DATA } from './query/Query';
import 'aos/dist/aos.css'

function App() {
  const { loading, error, data } = useQuery(GET_ALL_DATA);

  useEffect(() => {
    Aos.init();
  }, [])

  if (loading) return <Loader />
  if (error) {
    console.log('Wystąpił błąd:', error);
    return <Loader error />
  }
  return (
    <main id='app'>
      <HeroScreen socialmedia={data.allSocials} hero={data.hero.image} />
      <Nav />
      <Sphere words={data.sphere.words} />
      <About description={data.description} image={data.profileImg.mainPhoto} />
      {data.discount && <Discount data={data.discount} />}
      <Pricelist services={data.allServices} pricelist={data.allPricelists} />
      <Portfolio data={data.allPortfolios} />
      <Contact data={data.contact} />
      <Footer data={data.footer} />
    </main>
  )
}
export default App;