import styled from "styled-components";

export const Wrapper = styled.section`
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1440px;
    margin: 4rem auto;
    p.informations{
        margin-top: 1rem;
        padding: 0 1rem;
        text-align: center;
        u{
            cursor: pointer;
        }
    }
    p.small{
        font-size: 14px;
    }
    .badges{
        margin: 1rem 0;
        max-width: 1000px;
        display: flex;
        justify-content: space-evenly;
        flex-wrap: wrap;
    }
`;
export const Table = styled.table`
    max-width: 600px;
    width: 100%;
    border-radius: ${({ theme }) => theme.borderRadius};
    overflow: hidden;
    border: 0;
    border-collapse: collapse;
    text-align: left;
    th{
        padding: 0.5rem 1rem;
        font-weight: 500;
    }
    .header{
        background-color: ${({ theme }) => theme.colors.orangeLight};
        text-transform: uppercase;
        th{
            padding: 1rem;
            font-weight: bold;
        }
    }
    tbody {
        transition: transform .3s ease, opacity .2s ease;
        &.changing{
            opacity: 0;
            transform: translateX(100%);
        }
        tr{
            cursor: pointer;
            transition: background-color .2s ease, transform .2s ease;
            &:hover{
                background-color: ${({theme}) => theme.colors.orangeMedium};
            }
        }
    }
    .right{
        text-align: right;
        white-space: nowrap;
        font-weight: bold;
    }
`;