import React from 'react'
import { Icon } from '@iconify/react';
import styled, { keyframes } from 'styled-components';

export default function Loader({error = false}) {
    return (
        <StyledLoader>
            {error ? (
                <div>
                    <p>Nie można załadować danych strony.</p>
                    <p>Spróbuj ponownie później.</p>
                    <p>Jeśli błąd nie ustąpi skontaktuj się poprzez <a className='visible' href="https://www.facebook.com/Ruda-Elka-mobilna-krawcowa-100520851937656" target='_blank' rel='noreferrer'>Facebooka</a></p>
                </div>
            ) : (
                <Icon icon="akar-icons:arrow-clockwise" />
            )}
        </StyledLoader>
    )
}

const spinAnimation = keyframes`
    to{
        transform: rotate(360deg);
    }
`;
const StyledLoader = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: ${({ theme }) => theme.colors.white};
    font-size: 2rem;
    display: grid;
    place-items: center;
    text-align: center;
    svg{
        animation: ${spinAnimation} 1s linear infinite;
    }
    div{
        font-size: 1rem;
    }
`;