import React, { useEffect } from 'react'
import styled from 'styled-components';
import TagCloud from 'TagCloud'

export default function Sphere({words}) {
    const container = '.cloud';
    let texts = words.split(',');
    texts = texts.map(word => word.trim());
    
    useEffect(() => {
        const options = {
            radius: 340 / 2,
            initSpeed: 'slow',
            maxSpeed: 'slow'
        }
        TagCloud(container, texts, options)
    }, [container, texts]);
    return (
        <Wrapper id='top' className='cloud'></Wrapper>
    )
}
const Wrapper = styled.section`
    display: flex;
    justify-content: center;
    margin: 4rem 0;
    text-transform: uppercase;
    user-select: none;
`;
